

.App-header {
  background-color: rgb(156, 156, 148);
  width: 100%;
  height: 100%;
  position: absolute;
  align-items: center;
}

video {
  position: fixed;
  left: 0;
  right: 0;
}


.pic {
  position:relative;
  vertical-align: bottom;
  width: 100px;
  height: 100px;
}

button {
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
  letter-spacing: 1px;
  color: aliceblue;
  opacity: 70%;
  background-color: black;
  top: 83%;
  position:absolute;
  z-index: 10;
  width: 200px;
  height: 40px;
  padding: 1px;
}

.message {
  font-family:Georgia, 'Times New Roman', Times, serif;
  font-size: 14px;
  letter-spacing: 1px;
  color: aliceblue;
  opacity: 40%;
  background-color: black;
  top: 10%;
  position: fixed;
  text-align: center;
  z-index: 10;
  width: 12rem;
  height: 3.5rem;
  padding-top: 0.5rem;
}

.rectangle {
	position: relative;
	width: 220px;
	height: 300px;
  border-radius: 50%;
	background-color: transparent;
	border: 5px solid white;
	top: 0%;
	left: 0%;
	z-index: 10;
}

